
















































import {
  Component,
  Vue,
} from 'vue-property-decorator';

@Component({
  name: 'Slots',
})
export default class Slots extends Vue {
  get header() {
    return `
        <template
          #header="{ meetings }">
          <div>{{ meetings.date }}</div>
        </template>
      `;
  }

  get nextAndPrevious() {
    return `
        <template #button-previous>
          <button
            @click="previous">
            previous
          </button>
        </template>
        <template #button-next>
          <button
            @click="next">
            next
          </button>
        </template>
      `;
  }

  get upAndDown() {
    return `
        <template #button-up="{ isDisabled }">
          <button
            :disabled="isDisabled"
            @click="up">
            up
          </button>
        </template>
        <template
          #button-down="{ isDisabled }">
          <button
            :disabled="isDisabled"
            @click="down">
            down
          </button>
        </template>

        <${''}script>
        export default {
          methods: {
            up() {
              this.$refs.meetingSelector.previousMeetings();
              // (this.$refs.meetingSelector as Vue & { previousMeetings: () => void }).previousMeetings();
            },
            down() {
              this.$refs.meetingSelector.nextMeetings();
              // (this.$refs.meetingSelector as Vue & { nextMeetings: () => void }).nextMeetings();
            },
          },
        };
        </${''}script>
      `;
  }

  get meeting() {
    return `
        <template
          #meeting="{ meeting }">
          <div>{{ meeting.date }}</div>
        </template>
      `;
  }

  get loading() {
    return `
        <template
          #loading>
          Loading ...
        </template>
      `;
  }
}
